<template>
  <v-app>
    <v-main>
      <v-container>
        <v-row justify="center" content="center">
          <v-col cols="12" md="4">
            <v-card class="px-3 py-3">
              <v-card-title> TODO You're Works </v-card-title>
              <v-row justify="center" content="center">
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    label="what to do title?"
                    placeholder="Placeholder"
                    outlined
                    v-model="todo.title"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field
                    label="what to do content?"
                    placeholder="Placeholder"
                    outlined
                    v-model="todo.content"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-btn color="green" v-on:click="saveTODO()" block dark
                    >Save To DO</v-btn
                  >
                </v-col>
              </v-row>
              <div v-if="todos.length">
                <v-list flat subheader three-line>
                  <v-subheader>TO DO List</v-subheader>

                  <v-list-item-group multiple active-class="">
                    <div v-for="(item, index) in todos" :key="index">
                      <v-list-item>
                        <template v-slot:default="{active}">
                          <v-list-item-action>
                            <v-checkbox
                              :input-value="item.active"
                              v-model="item.active"
                            ></v-checkbox>
                          </v-list-item-action>

                          <v-list-item-content>
                            <v-list-item-title>{{
                              item.title
                            }}</v-list-item-title>
                            <v-list-item-subtitle>{{
                              item.content
                            }}</v-list-item-subtitle>
                          </v-list-item-content>
                        </template>
                      </v-list-item>
                    </div>
                  </v-list-item-group>
                </v-list>

                <v-btn color="red" @click="removeToDo()" dark block>
                  remove to do
                </v-btn>
              </div>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "App",

  components: {},

  data: () => ({
    todo: {
      title: "",
      content: "",
      active: false,
    },
    todos: [],
  }),
  watch: {
    todos: {
      handler() {
        localStorage.setItem('todos', JSON.stringify(this.todos));
      },
    },
  },
  methods: {
    saveTODO() {
      let emptytodo = {
        title: "",
        content: "",
        active: false,
      };

      if (this.todo.title.length > 0) {
        this.todos.push(this.todo);
      }

      this.todo = emptytodo;
    },
    removeToDo() {
      this.todos.forEach((todo, index) => {
        if (todo.active === true) {
          this.todos.splice(index);
        }
      });
    },
  },
  mounted() {
    if (localStorage.getItem("todos")) {
      this.todos = JSON.parse(localStorage.getItem("todos"));
    }
  },
};
</script>
